<script>
import { Link } from '@inertiajs/inertia-vue3'
import { Inertia } from '@inertiajs/inertia';

export default {
    components:{
        Link
    },
    data(){
        return{
            activeRoute:''
        }
    },
    methods:{
        activeroute(url_route){
            return this.activeRoute === url_route ?  'active' : '' 
        }
    },
    mounted(){
        this.activeRoute = route().current();
        Inertia.on("navigate", (event) => {
            this.activeRoute =  route().current() // or location.pathname.substr(1) || 'home';
        });
    }
}
</script>